import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import AppContext from "../../context/AppContext";

class Statystyki extends PureComponent {
  static contextType = AppContext;

  render() {
    const {
      trips,
      countries,
      places,
      airports,
      routes,
      airlines,
      aircrafts
    } = this.context;
    const liczbaWyjazdow = trips.filter(
      (trip: any) => trip.hidden !== true && !trip.isFutureTrip
    ).length;
    const liczbaWyjazdowPlan = trips.filter(
      (trip: any) => trip.hidden !== true && trip.isFutureTrip
    ).length;
    const liczbaKrajow = countries.filter(
      (country: any) => country.isAvailable && country.isPastCountry
    ).length;
    const liczbaKrajowPlan = countries.filter(
      (country: any) => country.isAvailable && country.isFutureCountry
    ).length;
    const liczbaMiejsc = places.filter(
      (place: any) => place.isAvailable && place.isPastPlace
    ).length;
    const liczbaLotnisk = airports.filter(
      (airport: any) => airport.isAvailable && airport.isPastAirport
    ).length;
    const liczbaLotniskPlan = airports.filter(
      (airport: any) => airport.isAvailable && airport.isFutureAirport
    ).length;
    const liczbaTras = routes.filter((route: any) => route.pastFlights > 0)
      .length;
    let liczbaLotow = 0;
    routes.forEach((route: any) => {
      liczbaLotow += route.pastFlights;
    });
    let liczbaLotowPlan = 0;
    routes.forEach((route: any) => {
      liczbaLotowPlan += route.futureFlights;
    });
    const liczbaLinii = airlines.filter(
      (airline: any) => airline.pastFlights > 0
    ).length;
    const linie = airlines
      .filter((airline: any) => airline.pastFlights > 0)
      .sort(
        (a: any, b: any) =>
          a.pastFlights === b.pastFlights
            ? a.name < b.name
              ? -1
              : 1
            : a.pastFlights > b.pastFlights
              ? -1
              : 1
      );
    const liczbaSamolotow = aircrafts.filter(
      (aircraft: any) => aircraft.pastFlights > 0
    ).length;
    const samoloty = aircrafts
      .filter((aircraft: any) => aircraft.pastFlights > 0)
      .sort(
        (a: any, b: any) =>
          a.pastFlights === b.pastFlights
            ? a.name < b.name
              ? -1
              : 1
            : a.pastFlights < b.pastFlights
              ? 1
              : -1
      );
    return (
      <div className="Statystyki">
        <div className="Statystyki_header">Statystyki</div>
        <p className="Statystyki_item">Wyjazdy: {liczbaWyjazdow}</p>
        <p className="Statystyki_item">Kraje: {liczbaKrajow}</p>
        <p className="Statystyki_item">Miejsca: {liczbaMiejsc}</p>
        <p className="Statystyki_item">Lotniska: {liczbaLotnisk}</p>
        <p className="Statystyki_item">Trasy lotnicze: {liczbaTras}</p>
        <p className="Statystyki_item">Loty: {liczbaLotow}</p>
        <p className="Statystyki_item">Linie lotnicze: {liczbaLinii}</p>
        <div className="Statystyki_item">
          <ul className="Statystyki_list">
            {linie.map((linia: any) => (
              <li key={linia.id}>
                {linia.name} ({linia.pastFlights})
              </li>
            ))}
          </ul>
        </div>
        <p className="Statystyki_item">Samoloty: {liczbaSamolotow}</p>
        <div className="Statystyki_item">
          <ul className="Statystyki_list">
            {samoloty.map((samolot: any) => (
              <li key={samolot.id}>
                {samolot.name} ({samolot.pastFlights})
              </li>
            ))}
          </ul>
        </div>
        <div className="Statystyki_header--plany">Plany</div>
        <p className="Statystyki_item">Wyjazdy: {liczbaWyjazdowPlan}</p>
        <p className="Statystyki_item">
          Kraje do odwiedzenia: {liczbaKrajowPlan}
        </p>
        <p className="Statystyki_item">Lotniska: {liczbaLotniskPlan}</p>
        <p className="Statystyki_item">Loty: {liczbaLotowPlan}</p>
      </div>
    );
  }
}

export default Statystyki;
