import * as React from "react";
import { PureComponent } from "react";
import MapPlace from "../MapPlace";

class MapPlaces extends PureComponent<any> {
  render() {
    const { places } = this.props;
    return places
      .filter((place: any) => place.isPastPlace)
      .map((place: any) => <MapPlace key={place.id} place={place} />);
  }
}

export default MapPlaces;
